import React from 'react'
import Users from './pages/Users';
import Dashboard from './pages/Dashboard';
import Error from "./pages/error";
import UserDetail from './pages/UserDetail';
import GameHistory from './pages/HistoryGame/index'
import Histories from './pages/Histories';

const routes = [
    {
        path: '/admin-advanced-caro/app/dashboard',
        exact: true,
        main: () => <Dashboard />
    },
    {
        path: '/admin-advanced-caro/app/users',
        exact: true,
        main: () => <Users />
    },
    {
        path: '/admin-advanced-caro/app/users/:id',
        exact: true,
        main: () => <UserDetail />
    },
    {
        path: '/admin-advanced-caro/app/histories',
        exact: true,
        main: () => <Histories />
    },
    {
        path: '/admin-advanced-caro/app/histories/:id',
        exact: true,
        main: () => <GameHistory />
    },
    {
        path: '',
        exact: false,
        main: () => <Error />
    },
]

export default routes;